<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>

    </el-breadcrumb>
    <!--  -->
    <el-card class="box-card">
      <el-row>
        <!--  -->
        <el-col :span="8">
            <el-input placeholder="" clearable v-model="queryinfo" class="input-with-select" style="width:300px" @clear="getOrdersList">
                <el-button slot="append" icon="el-icon-search" @click="getOrdersList"></el-button>
            </el-input>
          
        </el-col>
      </el-row>
      <!--  -->
      <el-table :data="orderList" style="width: 100%;" border>
        <!--  -->
        <el-table-column type="index" label="#"> </el-table-column>
        <el-table-column prop="order_number" label="" width="500px"></el-table-column>
        <el-table-column prop="order_price" label="" ></el-table-column>
        <el-table-column prop="order_pay" label="" >
          <el-tag type="success" v-if="orderList.order_pay == '1'"></el-tag>
          <el-tag type="danger" v-else></el-tag>
        </el-table-column>
        <el-table-column prop="is_send" label="" ></el-table-column>
        <el-table-column prop="create_time" label="" >
          <template slot-scope="scope">
            {{scope.row.create_time | dateFormat}}
          </template>
        </el-table-column>
        <!--  -->
        <el-table-column prop="order_number" label="" width="120px">
          <template slot-scope='scope'>
            <el-button type="primary" size="mini" icon="el-icon-edit" @click="editOrders(scope.row)"></el-button>
            <el-button type="success" size="mini" icon="el-icon-location" @click="locationTimeline(scope.row)"></el-button>
          </template>
        </el-table-column>
      
      </el-table>
      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="pagenum"
        :page-sizes="[5, 10, 20, 50]"
        :page-size="100"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total">
      </el-pagination>
     
    </el-card>
    <!--  -->
    <el-dialog
    title="" :visible.sync="timelineVisible" width="50%">
      <!--  -->
      <el-timeline>
        <!-- :timestamp="item.time"  item.context  -->
        <el-timeline-item
          v-for="(item, index) in timelineData"
          :key="index"
          :timestamp="item.time">
          {{item.context}}
        </el-timeline-item>
      </el-timeline>

    </el-dialog>
    <!--   -->
    <el-dialog
      title=""
      :visible.sync="editOrdersVisible"
      width="50%"
      @close="editOrdersClosed">
      <!--  -->
      <el-form label-width='100px' :model="editOrdersForm" :rules="editOrdersFormRules" ref="editOrdersFormRef">
        <el-form-item label="/" prop="address1">
          <!--  -->
          <el-cascader
   
            :options="cityData"
            v-model="editOrdersForm.address1"
            :props="editOrdersProps"
            clearable>
          </el-cascader>
            
       
        </el-form-item>
        <!--  -->
        <el-form-item label="" prop="address2">
          <el-input v-model="editOrdersForm.address2"></el-input>
        </el-form-item>
        
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editOrdersVisible = false"> </el-button>
        <el-button type="primary" @click="editOrdersVisible = false"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
// 
import cityData from "./citydata.js";
export default {
  
  data(){
    return{
      // 
      pagenum:1,
      pagesize:5,
      // 
      total:0,
      // 
      queryinfo:"",
      // 
      orderList:[],
      // 
      timelineVisible:false,
      // 
      timelineData:[],
      // 
      editOrdersVisible:false,
      // 
      editOrdersForm:{
        address1:[],
        address2:""
      },
      // 
      editOrdersFormRules:{
        address1: [
          { required: true, message: '', trigger: 'blur' }
        ],
        address2: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      // 
      cityData:cityData,
      // 
      editOrdersProps:{
        expandTrigger: 'hover' 
      }
     
      
    }
  },
  methods:{
    // 
    async getOrdersList(){
      // 
      const {data:res} = await  this.$http.get("orders",{
        params:{
          pagenum:this.pagenum,
          pagesize:this.pagesize,
          query:this.queryinfo
        }
      });
      console.log(res);
      // 
      if(res.meta.status !== 200){
        return this.$message.error("")
      }
      // 
      this.orderList = res.data.goods
      this.total = res.data.total
    },
    // 
    handleSizeChange(val){
      //
      console.log(val);
      // 
      this.pagesize= val
      // 
      this.getOrdersList()
      
    },
    // 
    handleCurrentChange(val){
      //
      this.pagenum = val
      // 
      this.getOrdersList()
    },
    // 
    async locationTimeline(row){
      console.log(row);
      
      // 
      const {data:res} = await this.$http.get("/kuaidi/804909574412544580")
      // 
      console.log(res);
      // 
      if(res.meta.status !== 200){
        return this.$message("")
      }
      // 
      this.timelineData = res.data
      
      this.timelineVisible = true
    },
    // 
    editOrders(row){
      this.editOrdersVisible = true
    },
    // 
    editOrdersClosed(){
      this.$refs.editOrdersFormRef.resetFields()
    },
    // 
    cityDataChange(){

    }
    
  },
  created(){
    // 
    this.getOrdersList()
    

  }

}
</script>

<style>

</style>